.row-2 {
    font-size: 14px;
    padding-top: 10px;
}
/* Clear floats after the columns */
.row-2:after {
    content: "";
    display: table;
    clear: both;
}
/* Create two equal columns that floats next to each other */
div.column-2 {
    float: left;
    width: 50%;
    padding: 10px;
    /*height: 300px;*/ /* Should be removed. Only for demonstration */
    text-align: left !important;
}

.column-header {
    padding-left: 15px;
}

.row-2 .column-2 { 
    height: 260px;
}
